.icon {
    margin-top: 10px;
    width: 30%;
    height: 140px;
    text-align: center;
    color: #282828;
    font-weight: 500;
}

.icon:hover {
    border-radius: 5px;
    background-color: rgba(255, 255, 255, .15);  
    backdrop-filter: blur(5px);
}
.iconImage {
    height: 100px;
}